<template lang="html">

  <div class="header">
    <h1>Formular za ocenjivanje kompetencija - Samoprocena
</h1>

  </div>

  <div class="podaciOZaposlenom">
    <table id="podaciTable">
      <tr>
        <td>Ime i prezime zaposlenog</td>
        <td>{{user.name}}</td>
      </tr>
      <tr>
        <td>Naziv radnog mesta zaposlenog</td>
        <td>
          <input type="text" name="" value="">
        </td>
      </tr>
      <tr>
        <td>Kategorija posla</td>
        <td>
          <input type="text" name="" value="">
        </td>
      </tr>
      <tr>
        <td>Organizacioni deo </td>
        <td>
          <input type="text" name="" value="">
        </td>
      </tr>
      <tr>
        <td>Period u kom je vršena procena</td>
        <td>
          <input type="text" name="" value="">
        </td>
      </tr>
      <tr>
        <td>Datum procene</td>
        <td>
          <input type="text" name="" value="">
        </td>
      </tr>
      <tr>
        <td>Ime pretpostavljenog:</td>
        <td>
          <input type="text" name="" value="">
        </td>
      </tr>
      <tr>
        <td>Naziv radnog mesta pretpostavljenog:</td>
        <td>
          <input type="text" name="" value="">
        </td>
      </tr>

    </table>
  </div>

  <div class="formular">
    <table id="formular">
      <tr>
        <th>Kompetence :</th>
        <th>Samoprocena</th>
      </tr>
      <tr>
        <td>Inicijativa</td>
        <td>
          <select name="type" id="type" v-model="inicijativa" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
      <tr>
        <td>Orijentisanost ka Klijentima</td>
        <td>
          <select name="type" id="type" v-model="orijentisanostKaKlijentima" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
      <tr>
        <td>Timski rad</td>
        <td>
          <select name="type" id="type" v-model="timskiRad" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
      <tr>
        <td>Lični razvoj i razvoj drugih</td>
        <td>
          <select name="type" id="type" v-model="licniRazvoj" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
      <tr>
        <td>Odnos prema radu, radnim zadacima i sredstvima rada</td>
        <td>
          <select name="type" id="type" v-model="odnos" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
      <tr>
        <td>Kvalitet</td>
        <td>
          <select name="type" id="type" v-model="kvalitet" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
      <tr>
        <td>Odgovornost</td>
        <td>
          <select name="type" id="type" v-model="odgovornost" >
             <option  v-for="odg in odgovori" v-bind:key="odg">{{odg}}</option>
          </select>
        </td>
      </tr>
    </table>
  </div>

  <div class="komentar">
    <h4>Komentar</h4>
    <input
      class="form-control"
      type="text"
      placeholder=""
      name="komentar"
      v-model="komentar"
      id="komentar"
      >
  </div>

  <div class="submitDiv">
  <button class="btn btn-primary">Submit</button>
  </div>


</template>

<script>
import {mapGetters} from 'vuex';
export default {
    computed: mapGetters(['user']),
    data(){
      return{
        odgovori:['Ne Zadovoljava Ocekivanja','Potrebno Poboljsanje','Zadovoljava Ocekivanja','Iznad Ocekivanja'],
        komentar:""
      }
    }
}
</script>

<style lang="css" scoped>
table, th, td{
  border: 1px solid black;
  text-align: center;
}
tr:nth-child(even){background-color: #bbbbbb;}
tr:nth-child(odd){background-color: #ffffff;}
tr:hover {background-color: #afa;}
table{
   margin: auto;
}
h4{
   text-align: center;
}
h1{
  text-align: center;
}
.header{
  background-color: #ffaaaa;
  padding: 15px;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-radius: 15px;
}
.podaciOZaposlenom{
  background-color: #ffffaa;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-top: 0px;
  border-radius: 15px;
  padding:10px
}
.formular{
  background-color: #dddd88;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-top: 0px;
  border-radius: 15px;
  padding:10px
}

.komentar{
  background-color: #ffffaa;
  border-style: solid;
  border-color: black;
  border-width: medium;
  border-top: 0px;
  border-radius: 15px;
  padding:10px
}
.submitDiv{
  width: 100%;
}
.btn{
  width: 100%;
  background-color: #003366;
}
</style>
